// fonts
$default-fonts: Arial, sans-serif;

@include font('Calibri', 'Calibri');

@mixin calibri {
	font-family: 'Calibri', $default-fonts; }

// colors
// $black: #000

@mixin trs {
	transition: all 0.2s; }


/* ===================================
	 Main Styles
	 @mixin ================================== */
body {
	position: relative;
	min-width: 320px;
	@include calibri;
	font-size: 16px;
	color: #001d31;
	line-height: 1.25;
	letter-spacing: 0.02em; }

.wrapper {
	position: relative;
	overflow: hidden;
	margin: 0 auto;
	min-height: 100%; }

.container {
	margin-left: auto;
	margin-right: auto;
	padding-left: 20px;
	padding-right: 20px;
	max-width: 1568px;
	@include pie-clearfix; }


/* Helpers
	----------------------------------- */
button {
	background-color: transparent; }

a:hover, a:focus {
	text-decoration: none; }

a {
	color: inherit;
	text-decoration: none; }

table {
	vertical-align: top; }

img {
	max-width: 100%;
	vertical-align: top; }

.icon {
	display: inline-block;
	vertical-align: middle; }

.text-left {
	text-align: left; }

.text-center {
	position: relative;
	text-align: center; }

.text-right {
	text-align: right; }

.is-hidden {
	display: none; }

.vertical {
	display: inline-block;
	vertical-align: middle; }

.cfx {
	@include pie-clearfix; }

// *
// 	+selection
// 		color: #fff
// 		background-color: $red

.btn {
	display: inline-block;
	padding-left: 30px;
	padding-right: 30px;
	height: 36px;
	white-space: nowrap;
	text-align: center;
	font-size: 14px;
	color: #ffffff;
	line-height: 36px;
	text-transform: uppercase;
	&_red {
		border: 1px solid #b91443;
		background-color: #b91443;
		@include trs;
		&:hover {
			background-color: transparent; } }
	&_white {
		border: 1px solid #FFFFFF;
		background-color: transparent;
		@include trs;
		&:hover {
			border-color: #b91443;
			background-color: #b91443; } }
	&_red-t {
		color: #b91443;
		border: 1px solid #b91443;
		@include trs;
		&:hover {
			color: #ffffff;
			background-color: #b91443; } }
	&_lg {
		padding-left: 48px;
		padding-right: 48px;
		border-width: 2px;
		height: 66px;
		font-size: 22px;
		line-height: 66px; }
	&_md {
		height: 51px;
		padding-left: 0;
		padding-right: 0;
		width: 278px;
		font-size: 18px;
		line-height: 51px; } }

.title {
	font-size: 30px;
	text-align: center;
	color: #b91443;
	text-transform: uppercase;
	letter-spacing: 0.24em;
	&_black {
		color: #000000; }
	&_white {
		color: #ffffff; } }



/* ===================================
	 Header
	 @mixin ================================== */
.header {
	position: fixed;
	z-index: 99;
	left: 0;
	top: 0;
	right: 0;
	color: #ffffff;
	.container {
		position: relative; }
	&__top {
		position: relative;
		height: 48px;
		text-align: center;
		background-color: #000000; }
	&__nav {
		position: relative;
		z-index: 1;
		padding-top: 14px; }
	&__phone {
		position: absolute;
		z-index: 1;
		top: 13px;
		left: 20px;
		font-size: 21px;
		color: #ffe9be;
		line-height: 1; }
	&__btn {
		position: absolute;
		z-index: 1;
		top: 5px;
		right: 20px; }
	&__bot {
		position: relative; }
	&__logo {
		float: left;
		margin-top: 13px; }
	&__info {
		float: right;
		padding-top: 60px;
		@include pie-clearfix; }
	&__block {
		float: right;
		font-size: 20px; }
	&__links {
		@include pie-clearfix; }
	&__link {
		float: left;
		text-decoration: underline;
		color: #ffffff;
		@include trs;
		&:hover {
			color: #f2d3b5; }
		& + & {
			margin-left: 15px; } } }

.soc {
	float: left;
	@include pie-clearfix;
	&__item {
		float: left;
		font-size: 0;
		margin-right: 15px;
		.icon {
			display: inline-block; }
		.icon + .icon {
			display: none; }
		&:hover {
			.icon {
				display: none; }
			.icon + .icon {
				display: inline-block; } } } }

.nav {
	display: inline-block;
	vertical-align: middle;
	font-size: 16px;
	line-height: 20px;
	text-transform: uppercase;
	@include pie-clearfix;
	&__link {
		float: left;
		margin-left: 22px;
		margin-right: 22px;
		color: #ffffff;
		@include trs;
		&:hover {
			color: #7c7c7c; } } }



/* ===================================
	 Footer
	 @mixin ================================== */
.footer {}


/* ===================================
	 Main
	 @mixin ================================== */
.main {}

/* Start
	----------------------------------- */
.start {
	position: relative;
	height: 725px;
	color: #ffffff;
	background: url('../img/bg/start.jpg') center center no-repeat;
	background-size: cover;
	&:after {
		pointer-events: none;
		content: '';
		position: absolute;
		top: -48px;
		left: 0;
		right: 0;
		height: 300px;
		width: 100%;
		background: url('../img/bg/header-bg.png') left top repeat-x; }
	&__container {
		height: 100%;
		@include vertical; }
	&__in {
		position: relative;
		display: inline-block;
		vertical-align: middle; }
	&__title {
		font-size: 49px;
		margin-bottom: 25px; } }

/* Service
	----------------------------------- */
.service {
	position: relative;
	padding-top: 75px;
	padding-bottom: 75px;
	background-color: #e3e4e6;
	&:before {
		pointer-events: none;
		content: '';
		position: absolute;
		top: -125px;
		left: 0;
		right: 0;
		height: 125px;
		background: url('../img/bg/start-bg.png') center center no-repeat;
		background-size: auto 100%; }
	&__btn {
		margin-top: 60px; }
	&__list {
		margin-top: 35px;
		margin-left: -20px;
		margin-right: -20px;
		@include pie-clearfix; }
	&__item {
		float: left;
		width: 20%;
		padding-left: 20px;
		padding-right: 20px; }
	&__image {
		position: relative;
		margin-left: -10px;
		margin-bottom: 35px;
		padding: 12px;
		width: 284px;
		height: 284px;
		border-radius: 50%;
		border: 1px solid #fff; }
	&__img {
		min-width: 100%;
		max-width: none;
		&-wrap {
			overflow: hidden;
			width: 100%;
			height: 100%;
			border-radius: 50%; } }
	&__subtitle {
		position: relative;
		margin-bottom: 25px;
		padding-left: 10px;
		font-size: 22px;
		color: #b91443;
		text-transform: uppercase;
		&:before {
			content: '';
			top: 10px;
			left: -20px;
			position: absolute;
			width: 10px;
			height: 10px;
			background-color: #b91443;
			transform: rotate(45deg); } }
	&__text {
		padding-left: 10px;
		padding-right: 20px; } }

/* Why
	----------------------------------- */
.why {
	padding-top: 75px;
	padding-bottom: 75px;
	background: #efd4bd url('../img/bg/why-bg.jpg') center top no-repeat;
	background-size: auto 100%;
	&__list {
		margin-top: 70px;
		font-size: 0; }
	&__item {
		display: inline-block;
		margin-bottom: 10px;
		padding-right: 70px;
		width: 30%;
		color: #001d31;
		vertical-align: top;
		&:nth-child(2) {
			width: 35%;
			margin-right: 20%;
			& ~ .why__item {
				vertical-align: bottom; } }
		&:nth-child(4) {
			padding-right: 30px; } }
	&__subtitle {
		position: relative;
		font-size: 21px;
		text-transform: uppercase;
		&:before {
			content: '';
			position: absolute;
			bottom: -11px;
			left: 0;
			width: 36px;
			height: 2px;
			background-color: #b91443; } }
	&__text {
		margin-top: 22px;
		font-size: 16px; }
	&__btn {
		position: relative;
		z-index: 2;
		margin-top: 70px; } }

.text {
	display: none;
	position: absolute;
	z-index: 1;
	top: 50px;
	left: 0;
	right: 0;
	margin-left: -100px;
	margin-right: -100px;
	background-color: #ffffff;
	box-shadow: 0 5px 5px rgba(0, 1, 1, 0.13);
	&__container {
		padding-top: 150px;
		padding-bottom: 80px; }
	&__row {
		padding-top: 75px;
		padding-bottom: 50px;
		text-align: left;
		@include pie-clearfix;
		& + & {
			border-top: 1px solid rgba(0,0,0,.2);
			padding-top: 50px;
			padding-bottom: 0; } }
	&__col2 {
		float: left;
		width: 50%; }
	&__col3 {
		float: left;
		width: 33.33%; }
	&__list {
		padding-right: 25px; }
	&__item {
		position: relative;
		padding-left: 20px;
		& + & {
			margin-top: 15px; }
		&:before {
			position: absolute;
			left: 0;
			top: 7px;
			width: 5px;
			height: 5px;
			border-radius: 50%;
			background-color: #b91443;
			content: ''; } }
	&__img {
		& + & {
			margin-left: 50px; } }
	&__text {
		& + & {
			margin-top: 25px; } } }

/* Work
	----------------------------------- */
.work {
	position: relative;
	background: url('../img/bg/work-bg.jpg') center center no-repeat;
	background-size: cover;
	padding-bottom: 6px;
	&:before {
		content: '';
		position: absolute;
		bottom: 0;
		left: 0;
		right: 0;
		height: 6px;
		background-color: #ffffff; }
	&-for {
		&__container {
			height: 594px;
			min-height: 594px;
			@include vertical;
			text-align: left; }
		&__in {
			margin-top: -60px;
			display: inline-block;
			vertical-align: middle;
			padding-left: 60px; }
		&__title {
			position: relative;
			font-size: 30px;
			color: #b91443;
			text-transform: uppercase;
			&:before {
				content: '';
				top: 15px;
				left: -40px;
				position: absolute;
				width: 10px;
				height: 10px;
				background-color: #b91443;
				transform: rotate(45deg); } }
		&__text {
			margin-top: 25px;
			max-width: 500px;
			font-size: 16px; } }
	&-nav {
		background-color: rgba(0, 0, 0, .8);
		&__container {
			font-size: 0; }
		&__item {
			position: relative;
			cursor: pointer;
			display: inline-block;
			margin-right: -1px;
			padding-top: 35px;
			width: 20%;
			height: 120px;
			text-align: center;
			font-size: 16px;
			color: #ffffff;
			line-height: 1.06;
			text-transform: uppercase;
			& + & {
				&:before {
					position: absolute;
					left: 0;
					top: 30px;
					height: 63px;
					width: 1px;
					background-color: #828281;
					content: ''; } }
			&.is-active {
				color: #b91443;
				background-color: #ffffff;
				z-index: 1;
				&:before {
					content: none;
					display: none; }
				&:after {
					position: absolute;
					top: -8px;
					left: 0;
					right: 0;
					background-color: #ffffff;
					height: 8px;
					content: ''; } } } } }


/* ===================================
	 Other Styles
	 @mixin ================================== */

