/* =================================== Main Styles @mixin ================================== */
body { position: relative; min-width: 320px; font-family: "Calibri", Arial, sans-serif; font-size: 16px; color: #001d31; line-height: 1.25; letter-spacing: 0.02em; }

.wrapper { position: relative; overflow: hidden; margin: 0 auto; min-height: 100%; }

.container { margin-left: auto; margin-right: auto; padding-left: 20px; padding-right: 20px; max-width: 1568px; }

.container:after { content: ""; display: table; clear: both; }

/* Helpers ----------------------------------- */
button { background-color: transparent; }

a:hover, a:focus { text-decoration: none; }

a { color: inherit; text-decoration: none; }

table { vertical-align: top; }

img { max-width: 100%; vertical-align: top; }

.icon { display: inline-block; vertical-align: middle; }

.text-left { text-align: left; }

.text-center { position: relative; text-align: center; }

.text-right { text-align: right; }

.is-hidden { display: none; }

.vertical { display: inline-block; vertical-align: middle; }

.cfx:after { content: ""; display: table; clear: both; }

.btn { display: inline-block; padding-left: 30px; padding-right: 30px; height: 36px; white-space: nowrap; text-align: center; font-size: 14px; color: #ffffff; line-height: 36px; text-transform: uppercase; }

.btn_red { border: 1px solid #b91443; background-color: #b91443; transition: all 0.2s; }

.btn_red:hover { background-color: transparent; }

.btn_white { border: 1px solid #FFFFFF; background-color: transparent; transition: all 0.2s; }

.btn_white:hover { border-color: #b91443; background-color: #b91443; }

.btn_red-t { color: #b91443; border: 1px solid #b91443; transition: all 0.2s; }

.btn_red-t:hover { color: #ffffff; background-color: #b91443; }

.btn_lg { padding-left: 48px; padding-right: 48px; border-width: 2px; height: 66px; font-size: 22px; line-height: 66px; }

.btn_md { height: 51px; padding-left: 0; padding-right: 0; width: 278px; font-size: 18px; line-height: 51px; }

.title { font-size: 30px; text-align: center; color: #b91443; text-transform: uppercase; letter-spacing: 0.24em; }

.title_black { color: #000000; }

.title_white { color: #ffffff; }

/* =================================== Header @mixin ================================== */
.header { position: fixed; z-index: 99; left: 0; top: 0; right: 0; color: #ffffff; }

.header .container { position: relative; }

.header__top { position: relative; height: 48px; text-align: center; background-color: #000000; }

.header__nav { position: relative; z-index: 1; padding-top: 14px; }

.header__phone { position: absolute; z-index: 1; top: 13px; left: 20px; font-size: 21px; color: #ffe9be; line-height: 1; }

.header__btn { position: absolute; z-index: 1; top: 5px; right: 20px; }

.header__bot { position: relative; }

.header__logo { float: left; margin-top: 13px; }

.header__info { float: right; padding-top: 60px; }

.header__info:after { content: ""; display: table; clear: both; }

.header__block { float: right; font-size: 20px; }

.header__links:after { content: ""; display: table; clear: both; }

.header__link { float: left; text-decoration: underline; color: #ffffff; transition: all 0.2s; }

.header__link:hover { color: #f2d3b5; }

.header__link + .header__link { margin-left: 15px; }

.soc { float: left; }

.soc:after { content: ""; display: table; clear: both; }

.soc__item { float: left; font-size: 0; margin-right: 15px; }

.soc__item .icon { display: inline-block; }

.soc__item .icon + .icon { display: none; }

.soc__item:hover .icon { display: none; }

.soc__item:hover .icon + .icon { display: inline-block; }

.nav { display: inline-block; vertical-align: middle; font-size: 16px; line-height: 20px; text-transform: uppercase; }

.nav:after { content: ""; display: table; clear: both; }

.nav__link { float: left; margin-left: 22px; margin-right: 22px; color: #ffffff; transition: all 0.2s; }

.nav__link:hover { color: #7c7c7c; }

/* =================================== Footer @mixin ================================== */
/* =================================== Main @mixin ================================== */
/* Start ----------------------------------- */
.start { position: relative; height: 725px; color: #ffffff; background: url("../img/bg/start.jpg") center center no-repeat; background-size: cover; }

.start:after { pointer-events: none; content: ''; position: absolute; top: -48px; left: 0; right: 0; height: 300px; width: 100%; background: url("../img/bg/header-bg.png") left top repeat-x; }

.start__container { height: 100%; font-size: 0; text-align: center; }

.start__container:before { content: ""; display: inline-block; vertical-align: middle; width: 0; height: 100%; }

.start__in { position: relative; display: inline-block; vertical-align: middle; }

.start__title { font-size: 49px; margin-bottom: 25px; }

/* Service ----------------------------------- */
.service { position: relative; padding-top: 75px; padding-bottom: 75px; background-color: #e3e4e6; }

.service:before { pointer-events: none; content: ''; position: absolute; top: -125px; left: 0; right: 0; height: 125px; background: url("../img/bg/start-bg.png") center center no-repeat; background-size: auto 100%; }

.service__btn { margin-top: 60px; }

.service__list { margin-top: 35px; margin-left: -20px; margin-right: -20px; }

.service__list:after { content: ""; display: table; clear: both; }

.service__item { float: left; width: 20%; padding-left: 20px; padding-right: 20px; }

.service__image { position: relative; margin-left: -10px; margin-bottom: 35px; padding: 12px; width: 284px; height: 284px; border-radius: 50%; border: 1px solid #fff; }

.service__img { min-width: 100%; max-width: none; }

.service__img-wrap { overflow: hidden; width: 100%; height: 100%; border-radius: 50%; }

.service__subtitle { position: relative; margin-bottom: 25px; padding-left: 10px; font-size: 22px; color: #b91443; text-transform: uppercase; }

.service__subtitle:before { content: ''; top: 10px; left: -20px; position: absolute; width: 10px; height: 10px; background-color: #b91443; transform: rotate(45deg); }

.service__text { padding-left: 10px; padding-right: 20px; }

/* Why ----------------------------------- */
.why { padding-top: 75px; padding-bottom: 75px; background: #efd4bd url("../img/bg/why-bg.jpg") center top no-repeat; background-size: auto 100%; }

.why__list { margin-top: 70px; font-size: 0; }

.why__item { display: inline-block; margin-bottom: 10px; padding-right: 70px; width: 30%; color: #001d31; vertical-align: top; }

.why__item:nth-child(2) { width: 35%; margin-right: 20%; }

.why__item:nth-child(2) ~ .why__item { vertical-align: bottom; }

.why__item:nth-child(4) { padding-right: 30px; }

.why__subtitle { position: relative; font-size: 21px; text-transform: uppercase; }

.why__subtitle:before { content: ''; position: absolute; bottom: -11px; left: 0; width: 36px; height: 2px; background-color: #b91443; }

.why__text { margin-top: 22px; font-size: 16px; }

.why__btn { position: relative; z-index: 2; margin-top: 70px; }

.text { display: none; position: absolute; z-index: 1; top: 50px; left: 0; right: 0; margin-left: -100px; margin-right: -100px; background-color: #ffffff; box-shadow: 0 5px 5px rgba(0, 1, 1, 0.13); }

.text__container { padding-top: 150px; padding-bottom: 80px; }

.text__row { padding-top: 75px; padding-bottom: 50px; text-align: left; }

.text__row:after { content: ""; display: table; clear: both; }

.text__row + .text__row { border-top: 1px solid rgba(0, 0, 0, 0.2); padding-top: 50px; padding-bottom: 0; }

.text__col2 { float: left; width: 50%; }

.text__col3 { float: left; width: 33.33%; }

.text__list { padding-right: 25px; }

.text__item { position: relative; padding-left: 20px; }

.text__item + .text__item { margin-top: 15px; }

.text__item:before { position: absolute; left: 0; top: 7px; width: 5px; height: 5px; border-radius: 50%; background-color: #b91443; content: ''; }

.text__img + .text__img { margin-left: 50px; }

.text__text + .text__text { margin-top: 25px; }

/* Work ----------------------------------- */
.work { position: relative; background: url("../img/bg/work-bg.jpg") center center no-repeat; background-size: cover; padding-bottom: 6px; }

.work:before { content: ''; position: absolute; bottom: 0; left: 0; right: 0; height: 6px; background-color: #ffffff; }

.work-for__container { height: 594px; min-height: 594px; font-size: 0; text-align: center; text-align: left; }

.work-for__container:before { content: ""; display: inline-block; vertical-align: middle; width: 0; height: 100%; }

.work-for__in { margin-top: -60px; display: inline-block; vertical-align: middle; padding-left: 60px; }

.work-for__title { position: relative; font-size: 30px; color: #b91443; text-transform: uppercase; }

.work-for__title:before { content: ''; top: 15px; left: -40px; position: absolute; width: 10px; height: 10px; background-color: #b91443; transform: rotate(45deg); }

.work-for__text { margin-top: 25px; max-width: 500px; font-size: 16px; }

.work-nav { background-color: rgba(0, 0, 0, 0.8); }

.work-nav__container { font-size: 0; }

.work-nav__item { position: relative; cursor: pointer; display: inline-block; margin-right: -1px; padding-top: 35px; width: 20%; height: 120px; text-align: center; font-size: 16px; color: #ffffff; line-height: 1.06; text-transform: uppercase; }

.work-nav__item + .work-nav__item:before { position: absolute; left: 0; top: 30px; height: 63px; width: 1px; background-color: #828281; content: ''; }

.work-nav__item.is-active { color: #b91443; background-color: #ffffff; z-index: 1; }

.work-nav__item.is-active:before { content: none; display: none; }

.work-nav__item.is-active:after { position: absolute; top: -8px; left: 0; right: 0; background-color: #ffffff; height: 8px; content: ''; }

/* =================================== Other Styles @mixin ================================== */
